/****************************************************************************************/
/* Backend editor widths setup and default max widths for Blocks not in C9 Grid Containers
/****************************************************************************************/
.block-editor-editor-skeleton__content .editor-styles-wrapper {
  margin: 0px auto;
  border-left: 1px solid #d6d6d6;
  border-right: 1px solid #d6d6d6; }

body.post-type-page .is-sidebar-opened .block-editor-editor-skeleton__content .editor-styles-wrapper {
  max-width: 1140px; }

@media only screen and (min-width: 768px) {
  .editor-styles-wrapper .wp-block {
    max-width: 960px; }
  .editor-styles-wrapper .is-root-container .wp-block[data-align="full"] {
    max-width: 100%;
    margin-left: 0px;
    margin-right: 0px; } }

@media only screen and (min-width: 992px) {
  .editor-styles-wrapper .wp-block {
    max-width: 1110px; } }

@media only screen and (min-width: 1200px) {
  .editor-styles-wrapper .wp-block {
    max-width: 1140px;
    margin-right: auto;
    margin-left: auto; }
  .editor-styles-wrapper .c9-grid .wp-block[data-type="c9-blocks/column-container"] .wp-block[data-type="core/button"],
  .is-sidebar-opened .editor-styles-wrapper .c9-grid [data-type="c9-blocks/column-container"].container .wp-block[data-type="core/button"] {
    margin-left: 0px;
    margin-right: 0px; }
  .editor-styles-wrapper .c9-grid .wp-block[data-type="c9-blocks/column-container"] .wp-block-buttons .wp-block[data-type="core/button"] {
    margin-right: 8px; }
  body.post-type-post .editor-styles-wrapper .wp-block {
    max-width: 900px; }
  .post-type-post .editor-styles-wrapper .is-root-container > .wp-block[data-align="wide"] {
    width: 1080px;
    margin-left: auto;
    margin-right: auto;
    max-width: 100vw; }
  .post-type-page .editor-styles-wrapper .is-root-container > .wp-block[data-align="wide"] {
    max-width: 1368px; }
  .post-type-page .is-sidebar-opened .editor-styles-wrapper .is-root-container > .wp-block[data-align="full"],
  .post-type-post .is-sidebar-opened .editor-styles-wrapper .is-root-container > .wp-block[data-align="full"] {
    max-width: calc(100vw - 280px);
    width: 100%;
    margin-left: 0;
    margin-right: 0; }
  .edit-post-layout:not(.is-sidebar-opened) .editor-styles-wrapper .block-editor-block-list__layout {
    padding-left: 0px;
    padding-right: 0px; } }

@media only screen and (min-width: 1500px) {
